<template>
  <div class="body">
    <!--回购-->
    <div class="flex" style="margin-top: 30px;">
      <div :class="[ 'state','flex', dealer==true?'bj':'']" @click="dealerInfo()">
        <div class="state2">平台强制回购</div>
      </div>
      <div :class="[ 'state','flex', members==true?'bj':'']" @click="member()" style="margin-left: 0px">
        <div class="state2">经销商申请回购</div>
      </div>
      <router-link to="/dealer/buyRecord">
        <div :class="[ 'state','flex']" style="margin-left: 0px">
          <div class="state2">回购记录</div>
        </div>
      </router-link>
    </div>
    <!--搜索-->
    <div class="flex whiteT">
      <div class="font">账号：</div>
      <div class="input">
        <el-input v-model="user_mobile" placeholder="输入账号"></el-input>
      </div>
      
      <div class="font">名称：</div>
      <div class="input">
        <el-input v-model="goods_name" placeholder="输入商品名称"></el-input>
      </div>
      
      <div class="font">购入日期：</div>
      <div class="input">
        <el-date-picker
          v-model="value1"
          style="width: 150px;"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择日期">
        </el-date-picker>
      </div>
      
      <div class="font" style="margin-left: 4%">至</div>
      <div class="input" style="margin-left: 2%">
        <el-date-picker
          v-model="value2"
          value-format="yyyy-MM-dd"
          style="width: 150px;"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </div>
      
      <!--按钮-->
      <div class="btn" style="margin-left:10%" @click="search()">
        <el-button type="primary" icon="el-icon-search">搜索</el-button>
      </div>
      <div class="btn2" style="margin-left: 5px" @click="res()">
        <el-button class="buttons3" icon="el-icon-refresh">重置</el-button>
      </div>
    
    </div>
    <!--表格头  平台强制回购 -->
    <div v-if="dealer==true">
      <div class="head join">
        <div class="head1" style="margin-left: 20px" @click="manage()">回购规则设置</div>
      </div>
      <!--表格-->
      <div>
        <el-table
          ref="multipleTable"
          :data="Pterrace"
          tooltip-effect="dark"
          style="width: 100%"
          border>
          <el-table-column
            type="index"
            label="序号"
            align="center"
            width="55">
          </el-table-column>
          <el-table-column
            label="用户ID"
            prop="user_id"
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            prop="user_mobile"
            label="账号"
            align="center"
            min-width="120">
          </el-table-column>
          <el-table-column
            label="商品信息"
            min-width="200"
            align="center"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.goods_name}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="商品单价"
            align="center"
            min-width="97">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.price}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="商品可获得积分"
            align="center"
            min-width="130">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.integral}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="create_time"
            label="购入时间"
            align="center"
            min-width="130">
          </el-table-column>
          <el-table-column
            label="购入数量"
            align="center"
            min-width="80">
            <template slot-scope="scope">
              <div v-for="(item,index) in scope.row.order_detail" :key="index">
                <div style="border: 1px solid #ddd">
                  {{item.num}}
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="day"
            label="购入天数"
            align="center"
            min-width="80">
          </el-table-column>
          <el-table-column
            label="操作"
            align="center"
            min-width="80"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div style="color: #0099ce;" @click="force(scope.row)">强制回购</div>
            </template>
          </el-table-column>
        </el-table>
        <div class="pag">
          <el-pagination
            @size-change="PhandleSizeChange"
            @current-change="PhandleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[20, 50, 100]"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total=totals>
          </el-pagination>
        </div>
      </div>
      <!--回购规则-->
      <div>
        <el-dialog
          title=""
          :visible.sync="centerDialogVisible"
          width="35%"
          center>
          <div class="xiu">
            <div class="edit">回购规则设置</div>
            <div class="flex">
              <div class="edit1" style="margin-left: 20%">强制回购期限：</div>
              <div class="edit2">
                <el-input v-model="Hday" placeholder="请输入天数"></el-input>
              </div>
            </div>
            <div class="flex">
              <div class="logBtn1" style="margin-left: 35%;">
                <el-button size="medium" type="primary" @click="submit()">提交</el-button>
              </div>
              <div class="logBtn2">
                <el-button size="medium">取消</el-button>
              </div>
            </div>
          </div>
        </el-dialog>
      </div>
    </div>
    <!--经销商申请回购-->
    <div v-else>
      <div class="head join">
      </div>
      <el-table
        ref="multipleTable"
        :data="Jterrace"
        tooltip-effect="dark"
        style="width: 100%"
        border>
        <el-table-column
          type="index"
          label="序号"
          align="center"
          width="55">
        </el-table-column>
        <el-table-column
          label="申请时间"
          prop="create_time"
          align="center"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="user_id"
          label="用户ID"
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="user_mobile"
          label="账号"
          min-width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="goods_name"
          label="商品信息"
          align="center"
          min-width="150">
        </el-table-column>
        <el-table-column
          prop="price"
          label="商品单价"
          align="center"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="goods_integral"
          label="商品可获得积分"
          align="center"
          min-width="130">
        </el-table-column>
        <el-table-column
          prop="order_time"
          label="购入时间"
          align="center"
          min-width="110">
        </el-table-column>
        <el-table-column
          prop="num"
          label="回购数量"
          align="center"
          min-width="80">
        </el-table-column>
        <el-table-column
          label="操作"
          align="center"
          min-width="100"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="flex">
              <div style="color: #0099ce;" @click="del(scope.row.id)">&nbsp;&nbsp;通过</div>
              <div style="color: #0099ce;" @click="del(scope.row.id)">&nbsp;&nbsp;驳回</div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="pag">
        <el-pagination
          @size-change="JPhandleSizeChange"
          @current-change="JPhandleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[20, 50, 100]"
          :page-size="5"
          layout="total, sizes, prev, pager, next, jumper"
          :total=Jtotal>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        input: '',
        value1: '',
        tableData3: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市'
        }],
        multipleSelection: [],
        centerDialogVisible: false,
        //  经销商信息选择
        dealer: true,
        members: false,
        //页码参数
        page: 1,
        pageSize: 10,
        currentPage4: 1,
        totals: 10,
        Jtotal: 10,
        //集合
        Pterrace: [],
        Jterrace: [],
        Hday: '',
        user_mobile: '',
        goods_name: '',
        value1: "",
        value2: ''
      }
    },
    methods: {
      manage() {
        this.centerDialogVisible = true;
      },
      //获取平台强制回购
      getdealer() {
        this._getData('/api/v1/dealer/buyback', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log('平台强制回购')
          console.log(data)
          this.Pterrace = data.data;
          this.totals = data.total;
        })
      },
      //每页显示多少数据
      PhandleSizeChange(val) {
        this.pageSize = val;
        this.getService();
      },
      //第几页
      PhandleCurrentChange(val) {
        this.page = val;
        this.getService();
      },
      //经销商页面
      JPhandleSizeChange(val) {
        this.pageSize = val;
        this.getJin();
      },
      //第几页
      JPhandleCurrentChange(val) {
        this.page = val;
        this.getJin();
      },
      //获取销商申请回购
      getJin() {
        this._getData('/api/v1/buyback_record/dealerApply', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log('经销商回购')
          console.log(data)
          this.Jterrace = data.data;
          this.Jtotal = data.total;
        })
      },
      //回购规则设置天数
      submit() {
        this._getData('/api/v1/buyback_record/buybackTime', {
            time: this.Hday,
          },
          data => {
            this.$message({
              type: 'success',
              message: '操作成功'
            });
            this.getdealer();
            this.centerDialogVisible = false;
          })
      },
      //强制回购
      force(val) {
        this.$confirm('是否强制回购此商品?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this._getData('/api/v1/buyback_record/coerce', {
              user_code: val.user_code,
              order_code: val.order_code,
              goodsId: val.goodsId,
              num: val.num,
              price: val.price,
            },
            data => {
              this.$message({
                type: 'success',
                message: '操作成功'
              });
              this.getdealer();
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      },
      //搜索
      search() {
        console.log(value1)
        console.log(value2)
        var data = this.goods_name;
        if (this.goods_name != '') {
          data.goods_name = this.goods_name;
        }
        if (this.user_mobile != '') {
          data.user_mobile = this.user_mobile;
        }
        if (this.value1 != '') {
          data.start_time = this.value1;
        }
        if (this.value2 != '') {
          data.end_time = this.end_time;
        }
        //获取平台
        this._getData('/api/v1/buyback_record/dealerApply', {
          page: this.page,
          pageSize: this.pageSize
        }, data => {
          console.log(data)
          this.Jterrace = data.data;
          this.totals = data.total;
        })
      },
      //平台申请
      dealerInfo() {
        this.getdealer();
        this.dealer = true;
        this.members = false;
      },
      //  经销商申请回购
      member() {
        this.dealer = false;
        this.members = true;
        this.getJin();
      }
    },
    created() {
      this.getdealer();
    }
  }
</script>

<style scoped>
</style>



